body {
  overflow: auto;
  background: -webkit-linear-gradient(315deg,#68c9b6,#3677ce) no-repeat;
  background: -o-linear-gradient(315deg,#68c9b6,#3677ce) no-repeat;
  background: linear-gradient(135deg,#68c9b6,#3677ce) no-repeat;
  background-attachment: fixed;
  margin: 0 10px;
}

a {
  text-decoration: none;
  color: #075dd6;
}

a:hover {
  text-decoration: underline;
}

li {
  margin: 4px 0;
}

.examples {
  border-bottom: 2px solid #E26E21;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 250ms ease-in;
  -o-transition: opacity 250ms ease-in;
  transition: opacity 250ms ease-in;
}


